  :is(.dark, .dark-theme) :where(.radix-themes:not(.light, .light-theme)) {
    --shadow-1:
      inset 0 -1px 1px 0 var(--gray-a3),
      inset 0 0 0 1px var(--gray-a3),
      inset 0 3px 4px 0 var(--black-a5),
      inset 0 0 0 1px var(--gray-a4);

    --shadow-2:
      0 0 0 1px color-mix(in oklab, var(--gray-a6), var(--gray-6)),
      0 0 1px 1px var(--gray-a5),
      0 1px 1px 0 var(--black-a6),
      0 2px 1px -1px var(--black-a6),
      0 1px 3px 0 var(--black-a8);

    --shadow-3:
      0 0 0 1px color-mix(in oklab, var(--gray-a6), var(--gray-6)),
      0 2px 3px -2px var(--black-a3),
      0 3px 8px -2px var(--black-a6),
      0 4px 12px -4px var(--black-a7);

    --shadow-4:
      0 0 0 1px color-mix(in oklab, var(--gray-a6), var(--gray-6)),
      0 8px 40px var(--black-a3),
      0 12px 32px -16px var(--black-a5);

    --shadow-5:
      0 0 0 1px color-mix(in oklab, var(--gray-a6), var(--gray-6)),
      0 12px 60px var(--black-a5),
      0 12px 32px -16px var(--black-a7);

    --shadow-6:
      0 0 0 1px color-mix(in oklab, var(--gray-a6), var(--gray-6)),
      0 16px 64px var(--black-a8),
      0 16px 36px -20px var(--black-a11);
    --card-classic-box-shadow-hover:
      0 0 0 1px color-mix(in oklab, var(--gray-a7), var(--gray-8)),
      0 0 1px 1px var(--gray-a7),
      0 0 1px -1px var(--gray-a4),
      0 0 3px -2px var(--gray-a3),
      0 0 12px -2px var(--gray-a3),
      0 0 16px -8px var(--gray-a9);
  }
}
[data-radius] {
  --radius-1: calc(3px * var(--scaling) * var(--radius-factor));
  --radius-2: calc(4px * var(--scaling) * var(--radius-factor));
  --radius-3: calc(6px * var(--scaling) * var(--radius-factor));
  --radius-4: calc(8px * var(--scaling) * var(--radius-factor));
  --radius-5: calc(12px * var(--scaling) * var(--radius-factor));
  --radius-6: calc(16px * var(--scaling) * var(--radius-factor));
}
[data-radius='none'] {
  --radius-factor: 0;
  --radius-full: 0px;
  --radius-thumb: 0.5px;
}
[data-radius='small'] {
  --radius-factor: 0.75;
  --radius-full: 0px;
  --radius-thumb: 0.5px;
}
[data-radius='medium'] {
  --radius-factor: 1;
  --radius-full: 0px;
  --radius-thumb: 9999px;
}
[data-radius='large'] {
  --radius-factor: 1.5;
  --radius-full: 0px;
  --radius-thumb: 9999px;
}
[data-radius='full'] {
  --radius-factor: 1.5;
  --radius-full: 9999px;
  --radius-thumb: 9999px;
}
.radix-themes:where([data-scaling='90%']) {
    --scaling: 0.9;
  }
.radix-themes:where([data-scaling='95%']) {
    --scaling: 0.95;
  }
.radix-themes:where([data-scaling='100%']) {
    --scaling: 1;
  }
.radix-themes:where([data-scaling='105%']) {
    --scaling: 1.05;
  }
.radix-themes:where([data-scaling='110%']) {
    --scaling: 1.1;
  }
@keyframes rt-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes rt-fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes rt-slide-up {
  from {
    transform: translateY(4px) scale(0.97);
  }
  to {
    transform: translateY(0) scale(1);
  }
}
@keyframes rt-slide-down {
  from {
    transform: translateY(-4px) scale(0.97);
  }
  to {
    transform: translateY(0) scale(1);
  }
}
@keyframes rt-slide-left {
  from {
    transform: translateX(4px) scale(0.97);
  }
  to {
    transform: translateX(0) scale(1);
  }
}
@keyframes rt-slide-right {
  from {
    transform: translateX(-4px) scale(0.97);
  }
  to {
    transform: translateX(0) scale(1);
  }
}
@media (prefers-reduced-motion: no-preference) {
  .rt-PopperContent {
    animation-timing-function: cubic-bezier(0.16, 1, 0.3, 1);
  }

    .rt-PopperContent:where([data-state='open']) {
      animation-duration: 300ms;
    }

      .rt-PopperContent:where([data-state='open']):where([data-side='top']) {
        animation-name: rt-slide-up, rt-fade-in;
      }
      .rt-PopperContent:where([data-state='open']):where([data-side='bottom']) {
        animation-name: rt-slide-down, rt-fade-in;
      }
      .rt-PopperContent:where([data-state='open']):where([data-side='left']) {
        animation-name: rt-slide-left, rt-fade-in;
      }
      .rt-PopperContent:where([data-state='open']):where([data-side='right']) {
        animation-name: rt-slide-right, rt-fade-in;
      }

    .rt-PopperContent:where([data-state='closed']) {
      animation-duration: 150ms;
    }

      .rt-PopperContent:where([data-state='closed']):where([data-side='top']) {
        animation-name: rt-slide-down, rt-fade-out;
      }
      .rt-PopperContent:where([data-state='closed']):where([data-side='bottom']) {
        animation-name: rt-slide-up, rt-fade-out;
      }
      .rt-PopperContent:where([data-state='closed']):where([data-side='left']) {
        animation-name: rt-slide-right, rt-fade-out;
      }
      .rt-PopperContent:where([data-state='closed']):where([data-side='right']) {
        animation-name: rt-slide-right, rt-fade-out;
      }
}
